.form-checkbox {
  font-size: 14px;
  line-height: 1.14286;
  display: inline-flex;
  vertical-align: top;
  align-items: flex-start;
  margin: 0;
}

.form-checkbox__input {
  position: absolute;
  clip: rect(0, 0, 0, 0);
}

.form-checkbox__fake {
  flex: none;
  width: 18px;
  height: 18px;
  padding: 4px;
  border: 1px solid #99CEFF;
  cursor: pointer;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.form-checkbox__fake::before {
  content: '';
  display: block;
  height: 100%;
  background: white;
  transition: 0.15s ease-in-out;
  transform: scale(0);
}

:focus ~ .form-checkbox__fake {
  outline: 0;
  box-shadow: 0 0 0 2px #66b6ff;
}

:checked ~ .form-checkbox__fake::before {
  transform: scale(1);
}

.form-checkbox__lbl {
  margin-top: 2px;
  margin-left: 14px;
}

.form-field-lbl {
