  font-size: 18px;
  line-height: 1.25;
  color: #99CEFF;
  text-transform: uppercase;
  white-space: nowrap;
  overflow: hidden;
  cursor: text;
  pointer-events: none;
  transition: 0.15s ease-in-out;
  position: absolute;
  top: 20.5px;
  right: 51px;
  left: 21px;
}

.form-field-lbl__input {
  font-weight: 300;
  font-size: 18px;
  line-height: 1.25;
  color: #99CEFF;
  text-transform: uppercase;
  display: block;
  width: 100%;
  padding: 34.5px 50px 6.5px 20px;
  border: 1px solid transparent;
  background: rgba(5, 11, 31, 0.2);
  box-shadow: 0 0 10px rgba(153, 206, 255, 0.2);
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.form-field-lbl__icon {
  color: #99CEFF;
  width: 20px;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  cursor: text;
  pointer-events: none;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

