.line-chart {
  overflow: hidden;
  position: relative;
}

.line-chart .peity {
  display: block;
}

.line-chart__inner {
  position: relative;
  margin-bottom: 65px;
}

.line-chart__peity {
  display: none;
}

.line-chart__ruler {
  position: absolute;
  right: 0;
  left: 0;
  height: 1px;
  background: linear-gradient(to right, #99CEFF 2px, transparent 3px) repeat-x 0 0/6px 100%;
}

.line-chart__ruler[data-ruler]::before {
  content: attr(data-currency) attr(data-ruler);
  font-weight: 100;
  font-size: 14px;
  line-height: 1.14286;
  color: white;
  position: absolute;
  bottom: 8px;
  left: 25px;
  cursor: default;
}

.line-chart__ruler--min {
  bottom: 0;
}

.line-chart__ruler--mid {
  bottom: 50%;
}

.line-chart__ruler--max {
  top: 0;
}

.line-chart__ruler--max::before {
  top: 8px;
  bottom: auto;
}

.line-chart__label {
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 1px;
  height: 100%;
  background: linear-gradient(to bottom, #99CEFF, rgba(153, 206, 255, 0.25));
  display: none;
  cursor: default;
}

.line-chart__label::before {
  content: '';
  width: 9px;
  height: 9px;
  border-radius: 50%;
  background: #99CEFF;
  border: 1px solid white;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%, -50%);
}

.line-chart__label-num {
  font-weight: 100;
  font-size: 14px;
  line-height: 1.14286;
  color: white;
  padding: 7px 10px;
  background: #2F7BC2;
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.line-chart__label-num::before {
  content: '';
  border-bottom: 6px solid #2F7BC2;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
}

.line-chart__label-num::after {
  content: '';
  border-top: 6px solid #2F7BC2;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
}

.line-chart__label--visible {
  display: block;
}

.line-chart__axis-x {
