.nav-meta__item, .nav-meta__item:hover, .nav-meta__item:focus {
  text-decoration: none;
}

.nav-meta__item:hover:not(.nav-meta__item--disabled) {
  color: white;
}

.nav-meta__item-col-1 {
  flex: none;
  width: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #2F7BC2;
}

.nav-meta__item-icon.isvg-ethereum {
  font-size: 37px;
}

.nav-meta__item-icon.isvg-metahash {
  font-size: 26px;
}

.nav-meta__item-icon.isvg-bitcoin {
  font-size: 32px;
}

.nav-meta__item-icon.isvg-bitcoin-cash {
  font-size: 21px;
}

.nav-meta__item-col-2 {
  display: flex;
  flex-flow: column;
  justify-content: center;
  min-width: 138px;
  padding: 5px 12px;
  background: #0A4982;
}

.nav-meta__item-title {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.16667;
  text-transform: uppercase;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.nav-meta__item-num {
  font-weight: 300;
  font-size: 24px;
  line-height: 1.16667;
  margin-top: 1px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.nav-meta__item--active {
  color: white;
  height: 100px;
}

.nav-meta__item--active .nav-meta__item-col-1 {
  width: 100px;
  position: relative;
}

.nav-meta__item--active .nav-meta__item-col-1::before {
  content: '';
  width: 17.47px;
  height: 17.47px;
  background: #2F7BC2;
  position: absolute;
  bottom: 0;
  left: 50%;
  margin: 0 0 -8.735px -8.735px;
  transform: rotate(45deg);
}

.nav-meta__item--active .nav-meta__item-icon {
  transform-origin: 50% 50% 0;
  transform: scale(1.53846);
}

.nav-meta__item--active .nav-meta__item-col-2 {
  min-width: 250px;
  background: rgba(47, 123, 194, 0.7);
}

.nav-meta__item--active .nav-meta__item-num {
  font-weight: 400;
  font-size: 28px;
  line-height: 1.17857;
  margin-top: 7px;
}

.nav-meta__item--disabled {
  cursor: default;
}

.nav-meta__item--disabled .nav-meta__item-title, .nav-meta__item--disabled .nav-meta__item-num {
  color: rgba(255, 255, 255, 0.3);
}

.page {
  height: 100%;
}

.page ::selection {
  color: white;
  background-color: #99CEFF;
}

.page__body {
  height: 100%;
}

.page__body-inner {
  display: flex;
  flex-direction: column;
  min-width: 1280px;
  min-height: 100%;
  background: url("../img/promo-spiral.png") no-repeat 0 0 fixed, url("../img/team-cloud.png") no-repeat 100% 100% fixed #03081C;
  padding-top: 70px;
}

.page__header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
}

.page__main {
  flex: 1 0 auto;
  padding: 30px 0;
}

.page__footer {
  flex: none;
}

.page--theme-dark .page__body-inner {
  background: url("../img/promo-spiral.png") no-repeat 0 0 fixed, url("../img/whatis-satelit.png") no-repeat right 185px top 290px fixed, url("../img/team-cloud.png") no-repeat 100% 100% fixed #03081C;
}

.section__header {
  margin-bottom: 50px;
}

.section__title {
  overflow: hidden;
  word-wrap: break-word;
}

.section__logo {
  display: block;
  font-size: 34px;
}

.section__actions {
  font-size: 14px;
  line-height: 1.14286;
}

.section__actions-item {
