// State
@mixin focus-default($shadows...) {
  //outline: 1px dotted;
  //outline: 5px auto -webkit-focus-ring-color;
  outline: 0;
  box-shadow: $shadows, $focus-box-shadow;
}

@mixin state-hover-focus {
  &:hover, &:focus {
    @content;
  }
}

@mixin state-active {
  &:not(.disabled):active {
    @content;
  }
}

@mixin state-disabled {
  &:disabled,
  &.disabled,
  fieldset[disabled] & {
    @content;
  }
}

// Pixel ratio
@mixin media-pixel-ratio($dpr) {
  @media (-webkit-min-device-pixel-ratio: $dpr), (min-resolution: $dpr*96dpi) {
    @content;
  }
}